<template>
  <main>
    <template v-if="!isRadio">
      <div class="chips-container" v-if="options && options.length > 0">
        <div
          v-bind:class="
            selectedOptions.includes(option[$attrs.value] || option.value) ||
            selected ||
            (defaultValue &&
              defaultValue.includes(option[$attrs.value] || option.value))
              ? 'selected chip'
              : 'chip'
          "
          v-for="(option, index) in options"
          v-on:click="toggleOption(option)"
          :key="index">
          <div class="chip-label">
            <span>{{ option[$attrs.label] || option.label }}</span>
          </div>
        </div>
      </div>
    </template>
    <template v-else>
      <div class="chips-container">
        <b-form-radio-group v-model="form[fieldName]" v-bind="$attrs">
          <b-form-radio
            v-for="(option, index) in options"
            :key="index"
            :value="option[$attrs.value]">
            <div
              class="chip d-flex align-items-center justify-content"
              :class="
                option[$attrs.value] == form[fieldName] ? 'selected' : ''
              ">
              <span class="chip-label font-size-smaller">
                {{ option[$attrs.label] }}
              </span>
            </div>
          </b-form-radio>
        </b-form-radio-group>
      </div>
    </template>
  </main>
</template>

<script>
export default {
  name: 'chipSelector',

  props: {
    options: {
      type: Array,
      required: true,
    },
    form: {
      type: Object,
      default: () => {},
    },
    fieldName: {
      type: String,
      default: '',
    },
    isRadio: {
      type: Boolean,
      default: false,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    defaultValue: {
      type: [Object, Array],
      required: false,
    },
  },

  data() {
    return {
      selectedOptions: [],
    };
  },
  methods: {
    toggleOption(option) {
      if (this.defaultValue) {
        this.selectedOptions = this.defaultValue;
        this.defaultValue = null;
      }
      if (
        !this.selectedOptions.includes(
          option[this.$attrs.value] || option.value
        )
      ) {
        this.selectedOptions.push(option[this.$attrs.value] || option.value);
      } else {
        this.selectedOptions.splice(
          this.selectedOptions.indexOf(
            option[this.$attrs.value] || option.value
          ),
          1
        );
      }
      this.$emit('update-selection', this.selectedOptions);
    },
  },
  mounted() {
    if (this.defaultValue) {
      this.selectedOptions = this.defaultValue;
    }
    console.log(this.options);
  },
};
</script>

<style lang="scss">
.chips-container {
  max-width: 100%;
  margin: 15px 0px;
  .close-icon {
    background: #0078ff;
    color: #e6f2ff;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    font-size: 11px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 9px;
    padding: 8px;
  }
  .chip {
    height: 36px;
    left: 0;
    top: 0;
    background: #ffffff;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    padding: 8px 12px;
    margin: 8px 12px 8px 0px;
    transition: all 300ms linear;
    color: #48535b;
    border: 1px solid #48535b;
    border-radius: 100px;
    .chip-label {
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      line-height: 20px;
      color: #48535b;
      margin: auto;
      vertical-align: middle;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
      text-align: center;
    }
    &.selected {
      background: rgba(0, 120, 255, 0.1);
      border: none;
      .chip-label {
        color: #0078ff;
      }
    }
  }
  .custom-control-label {
    &::before {
      display: none !important;
    }
    &::after {
      display: none !important;
    }
  }
  .custom-control {
    padding: 0 !important;
  }
  .custom-control-inline {
    margin: 0 !important;
  }
}
.chips-container .chip {
  border: 1px solid #c1c2c5;
}
.chips-container .chip .chip-label {
  color: #757e8d;
}
</style>
